const layoutStyle = {
  width: '100%',
  height: '60px',
  margin: 0,
  padding: 0,
  color: 'white',
  'background-color': 'rgb(0, 174, 239)',
  display: 'flex',
  'justify-content': 'center'
}

export default function Layout(props) {
  return (
    <div style={layoutStyle}>
      <div className="toolbar">
        <style jsx>{`
          .toolbar {
            width: 100%;
            max-width: 480px;
            display: flex;
          }
        `}</style>
        <h1>Hello Next.js</h1>
      </div>
    </div>
  )
}